















































import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class TextResult extends Vue {
  @Prop() data: any;
  @Prop() locale: any;
  @Prop() num!: number;
  saveTranslation(displayValueAlt: string) {
    this.$store.dispatch("saveTranslationsOnResult", {
      key: this.data.name,
      value: displayValueAlt
    });
  }
}
